var privateKeyXml = "<RSAKeyValue><Modulus></Modulus><Exponent>AQAB</Exponent><P>3SEXy03d2BzQuHQ4yYAvjytvkl2+qNbL6czJbcCQ225x5ccoxkGm6mgQjalCAZLCsTJjaDwYh9MGDy/odYFY+yDDXH2GbNdppNNKQBZ3bhkxQFEEkgXBtRfsl7OjPp3/mMCmRZ4UfMKnnyXYyb3DoOMpBmvp7WJ9fWcWTRHkTVU=</P><Q>2n56qoDvlCn/y3bK5OZ68RZiQSWUyzS2JTwIBtnCC8JTDRrviCCDsa/WdBTSPYN6mfiWNrQAqZQtUmSDmyuSlu8TueG0gu7LBuZvQzBETWlFx2i0llFpkBrteMEUvZrHnTcYm8CESbP8ClxiTtm8DTTLnWKtBwZ36PijlufjKos=</Q><DP>WjonkE/8ZjMTwz/A8A862Ks7gjh4FEJYv9RMmFjLgo2lMcG61HJzXhAKORNIGIY+pLjPsPdcdwdW4JmGu3OE0CtFK5cYY47dz2BbWp0QLD9hRkIIoGBy8qaJZjjpMiKb6UMqVRu0dleit6BkvaSNjzdBRRqBP9hBJgsLIXVcd2U=</DP><DQ>BKkqCLUTZz+2Gyr/jP51pOrrUaRxlhwbvpOXQD4o64NLrj3i2i6HYKT1mChRi8pgNSSeX7BbS0LD7LwEKq01RU74RsDPzvM1FYKY4QAIlPYn7OcI4yP9cnuAkVDdFmZU17+zIK9sov3Z5Iav+iqe3lE//q16L2ot6KRVKQTtTZ0=</DQ><InverseQ>d00QEr7Z883pfzWWYkPp5/IhCEZyIRZFutCWzmptNFVAsXpkPGG5vLaUblagKwu/wOaqXSEMbG6QckyhrFzQjlc/e9b9Z+q8PJNxBhRPBk1O/EJIvIugpoOoui/fySsgBQFeh3RNDqBLthAlg4Y78oq3vmKW3r5yK57IRgwP9bk=</InverseQ><D></D></RSAKeyValue>";
var publicKeyXml = "<RSAKeyValue><Modulus></Modulus><Exponent>AQAB</Exponent></RSAKeyValue>";

var GetNewRsaProvider = function() {
    return new ApzRSACryptoServiceProvider(2048);
}

var GetRsaKey = function(isEncryt : boolean) {
    var rsa = GetNewRsaProvider();
    rsa.FromXmlString(isEncryt ? publicKeyXml : privateKeyXml, isEncryt);
    return rsa.ExportParameters(isEncryt);
}

export function Encrypt(plainText : string) : string
{
    var decryptedBytes = ApzSystem.UnicodeEncoderGetBytes(plainText);
    var doOaepPadding = false;
    var rsa = GetNewRsaProvider();
    rsa.ImportParameters(GetRsaKey(true));
    var encryptedBytes = rsa.Encrypt(decryptedBytes, doOaepPadding);
    var encryptedString = ApzConverter.ToBase64String(encryptedBytes, false)
    return encryptedString;
}

export function Decrypt(sourceText : string) : string 
{
    var encryptedBytes = ApzConverter.FromBase64String(sourceText, false);
    var doOaepPadding = false;
    var rsa = GetNewRsaProvider();
    rsa.ImportParameters(GetRsaKey(false));
    var decryptedBytes = rsa.Decrypt(encryptedBytes, doOaepPadding);
    return ApzSystem.UnicodeEncoderGetString(decryptedBytes);
}


